<template>
  <div id="app">

    <headerComponent></headerComponent>

    <main>
      <section class="bg-secondary">
        <b-container>
          <b-row>
            <b-col col offset="0" offset-xl="3">
              <b-form class="search_top">
                <b-form-input type="text" aria-label="Text input with checkbox" class="" />
                <b-form-select v-model="selected" :options="options" class="">
                </b-form-select>
                <b-form-input type="date" class=""></b-form-input>
                <b-button variant="success" class="search_top__btn"> </b-button>
                <div class="search_top__favorites">f</div>
              </b-form>
            </b-col>
          </b-row>

        </b-container>
      </section>
      <b-container>
        <b-row>

          <b-col lg="3" class="mt-3">

            <b-form-group vertical label="Street:" label-class="text-sm-right" label-for="nestedStreet">
              <b-form-select v-model="selected" :options="options" id="nestedStreet" class="mb-3">
                <template slot="first">
                  <!-- this slot appears above the options from 'options' prop -->
                  <option :value="null" disabled>-- Please select an option --</option>
                </template>
                <!-- these options will appear after the ones from 'options' prop -->
                <option value="C">Option C</option>
                <option value="D">Option D</option>
              </b-form-select>
            </b-form-group>
            <b-form-group vertical label="City:" label-class="text-sm-right" label-for="nestedCity">
              <b-form-radio-group id="radios2" v-model="selected" name="radioSubComponent" stacked>
                <b-form-radio value="first"></b-form-radio>
                <b-form-radio value="second"></b-form-radio>
              </b-form-radio-group>
              <b-form-input id="nestedCity" type="range" min="100" max="200"></b-form-input>
            </b-form-group>
            <b-form-group vertical label="State:" label-class="text-sm-right" label-for="nestedState">
              <b-form-input type="text" aria-label="Text input with checkbox" class="w-25 d-inline-block" />
              <b-form-input type="text" aria-label="Text input with checkbox" class="w-25 d-inline-block" />
              <b-form-input id="nestedState" type="range"></b-form-input>
            </b-form-group>

          </b-col>

          <b-col lg="9">
            <div class="custom_map">
              <l-map ref="myMap">
              </l-map>
            </div>
            <b-breadcrumb :items="items" />
            <h2>    </h2>
            <p>- 12            , 
                   ,   .      ,
              ,   ,           .
               12           
              .         .

              :  BVI (356),  BVI (493), BVI (356),  BVI (493), BVI
              (356), ...
               :   (150),  (200),   (50)
               :   BVI,    BVI - ,     BVI</p>
            <yachtComponent></yachtComponent>
          </b-col>

        </b-row>
      </b-container>
    </main>

    <footerComponent></footerComponent>

  </div>
</template>

<script>
  import headerComponent from './components/header.vue';
  import footerComponent from './components/footer.vue';
  import yachtComponent from './components/yacht.vue';
  import {
    LMap,
    LTileLayer,
    LMarker
  } from 'vue2-leaflet';

  export default {
    name: 'app',
    data() {
      return {
        selected: null,
        options: [{
            value: null,
            text: 'Please select some item'
          },
          {
            value: 'a',
            text: 'This is First option'
          },
          {
            value: 'b',
            text: 'Default Selected Option'
          }
        ],
        items: [{
          text: '',
          href: '#'
        }, {
          text: ' ',
          href: '#'
        }, {
          text: ' ',
          active: true
        }]
      }
    },
    components: {
      LMap,
      LTileLayer,
      LMarker,
      headerComponent,
      footerComponent,
      yachtComponent,
      // RangeSlider,
      // HotelDatePicker,
    },
    mounted() {
      this.$nextTick(() => {
        this.$refs.myMap.mapObject.ANY_LEAFLET_MAP_METHOD();
      })
    }
  }
</script>

<style lang="scss">
  @import '../src/scss/_fonts.scss';
  @import "../node_modules/bootstrap/scss/functions";
  @import '../src/scss/_variables.scss';
  @import "../node_modules/bootstrap/scss/mixins";
  // Optional
  @import "../node_modules/bootstrap/scss/root";
  @import "../node_modules/bootstrap/scss/reboot";
  @import "../node_modules/bootstrap/scss/type";
  @import "../node_modules/bootstrap/scss/grid";
